<template>
  <div class="daily">
    <div class="title">今日开眼精选</div>
    <VideoItemCard
      v-for="(item, index) in  DailyVideosList"
      :item="item"
      :key="index"
      @VideoItemCardClick="VideoItemCardClick($event)"
    ></VideoItemCard>
    <div class="title">往期开眼精选</div>
    <VideoItemCard
      v-for="(item, index) in NextVideoList"
      :item="item"
      :key="index+`s`"
      @VideoItemCardClick="VideoItemCardClick($event)"
    ></VideoItemCard>

    <div ref="endmark" class="end">
      <div class="loading" v-if="isLoadingMore">
        <van-loading type="spinner" color="#6b6b6b" />
      </div>
      <div class="text" v-else @click="getMorePage">{{EndText}}</div>
    </div>
  </div>
</template>

<script>
import VideoItemCard from "@/components/VideoItemCard.vue";

export default {
  name: "Daily",
  data() {
    return {
      EndText: `加载更多内容`,
      NextPageArr: [],
      NextVideoList: [],
      NextPage: null,
      isLoadingMore: false
    };
  },
  methods: {
    getMorePage() {
      this.isLoadingMore = true;
      if (this.NextPage) {
        this.axios
          .get(`https://api.kele8.cn/agent/${this.NextPage}`)
          .then(res => {
            if (res.data.issueList) {
              this.NextPageArr = res.data.issueList[0].itemList;
            }
            this.NextPage = res.data.nextPageUrl;
          })
          .finally(() => {
            this.isLoadingMore = false;
          });
      }else{
        this.EndText='到底咯~~'
      }
    },
    VideoItemCardClick($event) {
      this.$router.push(`/playingdetailpage?id=${$event.data.id}`);
      this.$store.commit("setPlayingPageDate", $event);
      this.$store.commit("setShowBottomBar", false);
    }
  },
  watch: {
    ExactNextPageArr() {
      for (const key in this.ExactNextPageArr) {
        this.NextVideoList.push(this.ExactNextPageArr[key]);
      }
    }
  },
  computed: {
    ExactNextPageArr() {
      let arr = [];
      for (const key in this.NextPageArr) {
        if (this.NextPageArr[key].type == "video") {
          arr.push(this.NextPageArr[key]);
        }
      }
      return arr;
    },
    NextPageUrl() {
      return this.$store.state.RecommendedToday.nextPageUrl;
    },
    DailyData() {
      return this.$store.state.RecommendedToday;
    },
    DailyVideos() {
      if (this.DailyData.issueList) {
        return this.DailyData.issueList[0];
      } else {
        return [];
      }
    },
    DailyVideosList() {
      let arr = [];
      for (let key in this.DailyVideos.itemList) {
        if (this.DailyVideos.itemList[key].type == "video") {
          arr.push(this.DailyVideos.itemList[key]);
        }
      }
      return arr;
    }
  },

  created() {
    this.axios
      .get(`https://api.kele8.cn/agent/${this.NextPageUrl}`)
      .then(res => {
        if (res.data.issueList) {
          this.NextPageArr = res.data.issueList[0].itemList;
        }
        this.NextPage = res.data.nextPageUrl;
      });
  },

  components: {
    VideoItemCard
  }
};
</script>

<style lang="less" scoped>
.daily {
  .title {
    font-size: 18px;
    font-weight: bold;
  }
  .end {
    .text,
    .loading {
      padding: 10px 0;
      text-align: center;
    }
    .text{
      color:#6b6b6b;
    }
  }
}
</style>